import React, {Component} from 'react';
import Button from "@material/react-button";
import {Link} from "react-router-dom";

import {MdChevronLeft} from "react-icons/md"
import logoRow from "./img/logo_row.svg";

export default class Route404 extends Component {

	componentDidMount() {
		window.scrollTo(0, 0);
	}

	render() {

		const projectGridClass = (window.innerWidth < 530 ? "grid-container-project-mobile" : "grid-container-project");
		const footerClass = (window.innerWidth < 530 ? "bottom-nav footer button-container-shadow " : "bottom-nav button-container-bottom white button-container-shadow " + " button-desktop-back");

		return (
				<div className="grid-container">
					<div className={"full-grid-overlay " + " no-shadow"}>
						<div className={"map-logo-row"}>
							<img src={logoRow} className="jvrMapLogoRow"
							     alt="Jeff Van Rees Map Logo."/>
						</div>
						<div className={"aboutMeOpen"} style={{textAlign: "center"}}>

							<h1 >
								404 Page not found!
							</h1>
							<h2>
								Think something is supposed to
							</h2>
						</div>
						<div className={footerClass}>
							<Link to="/">
								<Button icon={<MdChevronLeft/>}>
									Head to Homepage
								</Button>
							</Link>
						</div>
					</div>
				</div>
		)
	}
}
